<template>
  <v-container fluid>
    <v-toolbar flat>
      <v-spacer></v-spacer>
      <v-btn @click.prevent="createNewStaff" color="primary">
        <v-icon class="mr-2" dark> mdi-plus-circle-outline </v-icon> Create
      </v-btn>
    </v-toolbar>
    <v-card flat class="pa-3 pa-sm-6 pt-0 pt-sm-0">
      <v-card-title class="pr-0">
        Staffs
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          outlined
        ></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="staffs" :search="search">
        <template v-slot:[`item.actions`]="{ item }">
          <v-btn
            :ripple="false"
            dark
            class="mr-2"
            small
            color="#fbbc05"
            elevation="0"
            fab
          >
            <v-icon @click="editItem(item)"> mdi-pencil </v-icon>
          </v-btn>
          <v-btn
            :ripple="false"
            dark
            class="mr-2"
            small
            color="#4285f4"
            elevation="0"
            fab
          >
            <v-icon @click="viewItem(item)"> mdi-eye </v-icon>
          </v-btn>
          <v-btn :ripple="false" dark small color="#ea4335" elevation="0" fab>
            <v-icon @click="deleteItem(item)"> mdi-delete </v-icon>
          </v-btn>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="initialize"> Reset </v-btn>
        </template></v-data-table
      >
    </v-card>
    <add-edit-staff
      v-if="isAddEditPopupOpen"
      v-model="isAddEditPopupOpen"
      :selected-staff="selectedStaff"
      @create-staff="createStaff"
      @update-staff="updateStaff"
    />
    <v-snackbar v-model="snackbar" :timeout="snackbarTimeout">
      {{ snackbarText }}
      <v-btn color="blue" text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <delete-confirmation-popup
      v-if="isDeleteConfimationPopupOpen"
      v-model="isDeleteConfimationPopupOpen"
      @delete-confirmation="deleteSelectedStaff"
    />
    <view-staff
      :staff="selectedStaff"
      v-if="isViewStaffPopupOpen"
      v-model="isViewStaffPopupOpen"
    />
  </v-container>
</template>

<script>
import AddEditStaff from "@/components/AddEditStaff.vue";
import DeleteConfirmationPopup from "@/components/DeleteConfirmationPopup.vue";
import ViewStaff from "@/components/ViewStaff.vue";

export default {
  name: "Staff",
  components: {
    AddEditStaff,
    DeleteConfirmationPopup,
    ViewStaff,
  },
  data() {
    return {
      search: "",
      snackbar: false,
      snackbarText: "",
      snackbarTimeout: 2000,
      isAddEditPopupOpen: false,
      selectedStaff: null,
      isDeleteConfimationPopupOpen: false,
      isViewStaffPopupOpen: false,
      headers: [
        {
          text: "First Name",
          align: "start",
          sortable: false,
          value: "firstName",
        },
        {
          text: "Last Name",
          align: "start",
          sortable: false,
          value: "lastName",
        },
        {
          text: "Branch Name",
          value: "branch",
          align: "center",
          sortable: false,
        },
        {
          text: "Designation",
          value: "designation",
          align: "center",
          sortable: false,
        },
        {
          text: "Patient's registered",
          value: "patientsCount",
          align: "center",
          sortable: false,
        },
        { text: "Action", value: "actions", align: "center", sortable: false },
      ],
      staffs: [
        {
          _id: "0",
          firstName: "Jerome",
          lastName: "Bell",
          branch: "CBE Branch",
          designation: "Doctor",
          patientsCount: 3,
          address: "India",
        },
        {
          _id: "1",
          firstName: "John",
          lastName: "Doe",
          branch: "CBE Branch",
          designation: "Staff",
          patientsCount: 6,
          address: "India",
        },
        {
          _id: "2",
          firstName: "Jane",
          lastName: "Doe",
          branch: "CBE Branch",
          designation: "Nurse",
          patientsCount: 9,
          address: "India",
        },
        {
          _id: "3",
          firstName: "Jerome",
          lastName: "Bell",
          branch: "CBE Branch",
          designation: "Doctor",
          patientsCount: 3,
          address: "India",
        },
        {
          _id: "4",
          firstName: "John",
          lastName: "Doe",
          branch: "CBE Branch",
          designation: "Staff",
          patientsCount: 6,
          address: "India",
        },
        {
          _id: "5",
          firstName: "Jane",
          lastName: "Doe",
          branch: "CBE Branch",
          designation: "Nurse",
          patientsCount: 9,
          address: "India",
        },
        {
          _id: "6",
          firstName: "Jerome",
          lastName: "Bell",
          branch: "CBE Branch",
          designation: "Doctor",
          patientsCount: 3,
          address: "India",
        },
      ],
    };
  },
  methods: {
    editItem(staff) {
      this.selectedStaff = staff;
      this.isAddEditPopupOpen = true;
    },
    viewItem(staff) {
      this.selectedStaff = staff;
      this.isViewStaffPopupOpen = true;
    },
    deleteItem(staff) {
      this.selectedStaff = staff;
      this.isDeleteConfimationPopupOpen = true;
    },
    createNewStaff() {
      this.selectedStaff = null;
      this.isAddEditPopupOpen = true;
    },
    createStaff(staff) {
      this.staffs.push(staff);
      this.snackbar = true;
      this.snackbarText = "Staff created successfully";
      this.isAddEditPopupOpen = false;
    },
    updateStaff(staff, _id) {
      this.staffs.forEach((element, index) => {
        if (element._id === _id) {
          Object.assign(this.staffs[index], staff);
          this.snackbar = true;
          this.snackbarText = "Staff updated successfully";
        }
      });
      this.isAddEditPopupOpen = false;
    },
    deleteSelectedStaff() {
      this.staffs = this.staffs.filter(
        (staff) => staff._id !== this.selectedStaff._id
      );
      this.selectedStaff = null;
      this.isDeleteConfimationPopupOpen = false;
      this.snackbar = true;
      this.snackbarText = "Staff deleted successfully";
    },
  },
};
</script>

